body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

::selection {
  background-color: rgba(255, 114, 114,0.3);
}
.link {
    text-decoration: none;
    color: orange;
    padding: 0 1rem 0 1rem;
    line-height: 40px;
}

.link:hover {
    color: rgb(255, 114, 114);
}

.nav {
    background-color: rgba(255, 248, 220, 0.5) !important;
    position:-webkit-sticky;
    position:sticky;   
}

.is-active {
    font-weight: bold;
    background-color: rgb(254, 236, 178);
    border-radius: 1rem;
}
.Footer {
    background-color: rgba(255, 114, 114, 1);
    color: cornsilk;
    padding: 0.5rem 0;
}
#home-id {
    background-image: url(/SABKI/static/media/background-8.262dd6f9.svg);
    background-position: center center;
    background-repeat: repeat-y;
    background-size: cover;
}

#home2 {
    background-image: url(/SABKI/static/media/background-9.1fc234b3.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

#btn-survey {
    padding: 0.5rem 1rem;
    border: 0.15rem solid rgba(255, 114, 114, 1);
    border-radius: 1rem;
    background-color: rgba(255, 114, 114, 1);
    color: cornsilk;
    justify-self: left !important;
    margin: 0.5rem 1rem;
    float: left;
    letter-spacing: 0.1rem;
    transition: 0.3s;
}

#btn-survey:hover {
    background-color: rgba(255, 114, 114,0.0);
    border: 0.15rem solid rgba(255, 114, 114,1);
    color: rgba(255, 114, 114,1);
    
}

.ic-survey {
    font-size: 1.2rem;
    margin-left: 0.5rem;
}

#btn-panduan {
    padding: 0.5rem 1rem;
    border: 0.15rem solid rgba(255, 114, 114, 1);
    border-radius: 1rem;
    background-color: rgba(255, 114, 114, 1);
    color: cornsilk;
    justify-self: left !important;
    margin: 0.5rem 1rem;
    float: left;
    letter-spacing: 0.1rem;
    transition: 0.3s;
}

#btn-panduan:hover {
    background-color: rgba(255, 114, 114,0.0);
    border: 0.15rem solid rgba(255, 114, 114,1);
    color: rgba(255, 114, 114,1);
}
form {
    background-color: rgba(255, 248, 220, 0.5);
    padding: 2rem;
    border-radius: 4rem;
    margin: 2rem 0;
}

form select, input {
    border-color: rgba(255, 114, 114,1) !important;
}

form select:focus, input:focus {
    box-shadow: 0px 0px 0px 0.35rem rgba(255, 114, 114,0.3) !important;
}

.form-group {
    margin: 1rem 0;
    text-align: left;
}

.form-group label {
    margin-bottom: 0.5rem;
}

.btn-primary {
    padding: 0.5rem 1rem;
    border: 0.15rem solid rgba(255, 114, 114, 1);
    border-radius: 1rem;
    background-color: rgba(255, 114, 114, 1);
    color: cornsilk;
    justify-self: left !important;
    margin: 0 1rem;
    float: left;
    letter-spacing: 1px;
    transition: 0.3s;
}

.btn-primary:hover {
    background-color: rgba(255, 114, 114,0.0);
    border: 0.15rem solid rgba(255, 114, 114,1);
    color: rgba(255, 114, 114,1);
}

.btn-primary:active {
    background-color: rgba(255, 114, 114,0.0) !important;
    border: 0.15rem solid rgba(255, 114, 114,1) !important;
    color: rgba(255, 114, 114,1) !important;
}

.btn-submit {
    padding: 0.5rem 1rem;
    border: 0.15rem solid rgba(255, 114, 114, 1);
    border-radius: 1rem;
    background-color: rgba(255, 114, 114, 1);
    color: cornsilk;
    justify-self: left !important;
    margin: 0 1rem;
    letter-spacing: 1px;
    transition: 0.3s;
}
.btn-submit:hover {
    background-color: rgba(255, 114, 114,0.0);
    border: 0.15rem solid rgba(255, 114, 114,1);
    color: rgba(255, 114, 114,1);
}

.btn-submit:active {
    background-color: rgba(255, 114, 114,0.0) !important;
    border: 0.15rem solid rgba(255, 114, 114,1) !important;
    color: rgba(255, 114, 114,1) !important;
}
.Panduan {
    padding: 2rem 0;
}

.Panduan h1 {
    color: rgb(255, 114, 114);
    letter-spacing: 3px;
    font-weight: bold;
}

.daftar_anak {
    color:rgb(255, 114, 114);
    font-weight: bold;
    letter-spacing: 2px;
}
